.basketPage {
    margin-top: 175px;

    h4 {
        margin-bottom: 10px;
        font-size: 46px;
        text-transform: uppercase;
        color: $whiteColor;
    }

    hr {
        margin-top: 0;
        margin-bottom: 50px;
        max-width: 400px;
        height: 7px;
        background-color: $mainActiveColor;
    }

    .basketContent {
        display: flex;
        flex-direction: column;
    }

    .basketItem {
        margin-bottom: 30px;
        padding: 20px 45px 45px 45px;
        background-color: $pcBg;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }

    .itemInfo {
        width: 40%;

        h5 {
            margin-bottom: 50px;
            font-size: 40px;
            color: $whiteColor;
        }
    }

    .detailsPC {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 60px;
        grid-column-gap: 70px;

        .nameOfDetail {
            font-size: 20px;
            text-transform: uppercase;
            color: $mainSvgColor;
        }

        .aboutDetail {
            margin: 0;
            font-size: 20px;
            color: $whiteColor;
        }

        .detailPC {
            max-width: 360px;
            display: flex;

            svg {
                margin-top: 10px;
                margin-right: 20px;
                min-width: 30px;
                max-width: 30px;
            }
        }

        @media only screen and (max-width: 1280px) {
            grid-row-gap: 30px;
            grid-column-gap: 10px;
        }
    }

    .aboutPrice {
        margin: 0 30px;
        min-width: 260px;
        padding-top: 30px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: $mainBgColor;

        p,
        label,
        button {
            padding: 0 26px;
        }

        p:first-of-type {
            font-size: 20px;
            color: $mainSvgColor;
            text-transform: uppercase;
        }

        .price {
            font-size: 30px;
            color: $whiteColor;
        }

        p:last-of-type {
            font-size: 20px;
            color: $whiteColor;
        }

        .itemCount {
            margin-bottom: 32px;
            border: 1px solid $mainSvgColor;
            width: 86px;
            height: 45px;
            font-size: 20px;
            line-height: 31px;
            font-family: Consolas, sans-serif;
            background: none;
            text-align: center;
            color: $whiteColor;
        }


        .removeItem {
            height: 55px;
            border: 1px solid $mainActiveColor;
            font-size: 20px;
            color: $whiteColor;

            &:hover {
                border: 1px solid transparent;
                background-color: $mainActiveColor;
                color: $lighterGrayColor;
            }
        }
    }

    .forImg {
        max-width: 300px;
        min-width: 300px;
        min-height: 351px;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        img[src*="pc1"] {
            filter: drop-shadow(1px 1px 35px rgba(220, 118, 255, 0.5));
        }

        img[src*="pc2"] {
            filter: drop-shadow(0 0 35px rgba(0, 64, 255, 0.5));
        }

        img[src*="pc3"] {
            filter: drop-shadow(0 0 35px rgba(255, 140, 0, 0.5));
        }

        img[src*="pc4"] {
            filter: drop-shadow(0 0 35px rgba(98, 0, 255, 0.5));
        }

        img[src*="pc5"] {
            filter: drop-shadow(0 0 35px rgba(255, 72, 0, 0.5));
        }

        img[src*="pc6"] {
            filter: drop-shadow(0 0 35px rgba(0, 255, 144, 0.5));
        }

        img[src*="keyb"] {
            filter: drop-shadow(0 0 35px rgba(255, 140, 0, 0.5));
        }
    }

    .flexStats {
        padding-left: 30px;
        max-width: 412px;
        display: flex;
        flex-direction: column;

        > div {
            border-bottom: 2px solid $borderBottomColor;
            margin-bottom: 30px;
            display: flex;
            justify-content: space-between;
        }

        span {
            font-size: 20px;
        }
    }

    @media only screen and (max-width: 1280px) {
        .basketItem {
            flex-wrap: wrap;
            justify-content: space-between;

            .itemInfo {
                width: 100%;
            }
        }
    }

    @media only screen and (max-width: 1024px) {
        margin-top: 40px;

        .cont {
            h4 {
                font-size: 30px;
            }

            hr {
                height: 4px;
            }
        }

        .basketItem {
            flex-direction: column;
            align-items: center;

            h5 {
                margin-bottom: 25px;
                font-size: 30px;
            }

            .forImg {
                margin-bottom: 40px;
                order: 0;
                min-height: initial;
                height: 230px;
                width: 100%;
                min-width: initial;
            }

            .itemInfo {
                margin-bottom: 40px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                order: 1;

                .flexStats {
                    max-width: 100%;
                    padding-left: 0;

                    span {
                        font-size: 16px;
                    }
                }

                .detailsPC {
                    p {
                        font-size: 16px;
                    }
                }
            }

            .aboutPrice {
                margin: 0;
                order: 2;

                p {
                    font-size: 20px;
                }

                .itemCount {
                    font-size: 20px;
                }
            }
        }
    }

    @media only screen and (max-width: 860px) {
        .basketItem {
            padding: 0 15px;

            .forImg {
                max-width: 250px;
            }

            &.pc {
                .itemInfo {
                    align-items: center;

                    .detailsPC {
                        grid-template-columns: repeat(1, 1fr);

                        .detailPC {
                            max-width: 100%;
                        }
                    }
                }
            }
        }
    }
}